<template>
    <div>
        <div class="d-grid gap-2">
            <button class="btn btn-outline-primary" type="button" @click="draw">抽</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Clickbtn',
    props:{
        draw_type: String,
        draw_count: String,
        total_count: Number,
        total_each_count: Array,
        total_count_no: Number

    },
    data(){
        return{
            this_draw_count: this.draw_count,
            this_draw_type: this.draw_type,
            draw_array:[],
            card_detail:
            {
                "0": 
                {
                    "name":"夢詠守望 ‧ 英格麗",
                    "c":"5",
                    "src":"https://i.imgur.com/Qex60jq.jpg"
                },
                "1":
                {
                    "name":" 緋曦赤霞 ‧ 紅璦",
                    "c":"2",
                    "src":"https://i.imgur.com/YNkZD8A.jpg"
                },
                "2":
                {
                    "name":"比擬萬象 ‧ 達格",
                    "c":"3",
                    "src":"https://i.imgur.com/NrMUjwO.jpg"
                },
                "3":
                {
                    "name":"水幻冰藍 ‧ 米菲波",
                    "c":"1",
                    "src":"https://i.imgur.com/ssgcz6l.jpg"
                },
                "4":
                {
                    "name":"黛蔻藥師 ‧ 茜蘿珊",
                    "c":"3",
                    "src":"https://i.imgur.com/PhmqsYz.jpg"
                },
                "5":{
                    "name":"天才使魔 ‧ 豹豹",
                    "c":"4",
                    "src":"https://i.imgur.com/laMBtZh.jpg"
                },
                "6":{
                    "name":"破幻銀鈴 ‧ 傾霞",
                    "c":"4",
                    "src":"https://i.imgur.com/B6Gjqvq.jpg"
                },
                "7":{
                    "name":"無微珍護 ‧ 曼特裘",
                    "c":"5",
                    "src":"https://i.imgur.com/LgzuGQl.jpg"
                }
            },
            this_total_count: this.total_count,
            this_total_each_count: this.total_each_count,
            this_total_count_no: this.total_count_no
        }
    },
    methods:{
        draw(){
            var waiting_array = new Array();
            var new_draw_array = new Array();
            this.draw_array = new_draw_array;
            var c1, c2, c3;
            if(this.this_draw_type == 0){
                c1 = 10; c2 = 25; c3 = 188;
            }else{
                c1 = 25; c2 = 100; c3 = 155;
            }
            for(var i1 = 0; i1 < c1; i1++){
                waiting_array.push(0);
            }
            for(var j2 = 1; j2 <= 2; j2++){
                for(var i2 = 0; i2 < c2; i2++){
                    waiting_array.push(j2);
                }
            }
            for(var j3 = 3; j3 <= 7; j3++){
                for(var i3 = 0; i3 < c3; i3++){
                    waiting_array.push(j3);
                }
            }

            for(var draw_count = 0; draw_count < this.this_draw_count; draw_count++){
                var random_number=Math.floor(Math.random()*(waiting_array.length));
                var picked_item = waiting_array[random_number];
                this.this_total_count = parseInt(this.this_total_count,10) + 1;
                if(this.this_total_count == 35){
                    picked_item = 0;
                    this.this_total_count = 0;
                }
                new_draw_array.push({'id':draw_count, 'name':this.card_detail[picked_item].name, 'src':this.card_detail[picked_item].src, 'index':picked_item});
                this.this_total_each_count[picked_item].count += 1;
            }
            this.this_total_count_no = parseInt(this.this_total_count_no,10) + parseInt(this.this_draw_count,10);
            this.draw_array = new_draw_array;
            this.update_draw_array();
        },
        update_draw_array(){
            this.$emit("updated_draw", {'array':this.draw_array,'count':this.this_total_count,'total_array':this.this_total_each_count, 'no_count': this.this_total_count_no});
        }
    },
    watch:{
        draw_count: function(){
            this.this_draw_count = this.draw_count
        },
        draw_type: function(){
            this.this_draw_type = this.draw_type
        }
    },
}
</script>

<style></style>