<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">
      <div class="container-fluid">
        <a class="navbar-brand" href="#">抽卡模擬器v3</a>
        <form class="d-flex">
          <div class="btn-group" role="group" aria-label="Basic mixed styles example">
            <button class="btn btn-success" type="button" disabled>{{total_count}}</button>
            <button class="btn btn-warning" type="button" disabled>{{total_count_no}}</button>
          </div>
        </form>
      </div>
    </nav>
    <br><br><br>
    <div class="container">
      <nav>
        <div class="nav nav-tabs" id="nav-tab" role="tablist">
          <a class="nav-link active" id="nav-home-tab" data-bs-toggle="tab" href="#nav-home" role="tab" aria-controls="nav-home" aria-selected="true">抽卡</a>
          <a class="nav-link" id="nav-profile-tab" data-bs-toggle="tab" href="#nav-profile" role="tab" aria-controls="nav-profile" aria-selected="false">設定</a>
        </div>
      </nav>
      <div class="tab-content" id="nav-tabContent">
        <div class="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
          <br>
          <Clickbtn @updated_draw="self_update_draw" :draw_count="sellected_times" :draw_type="sellected_type" :total_each_count="total_each_count" :total_count="total_count" :total_count_no="total_count_no" />
          <br>
          <Show :pass_in_array="draw_array" :pass_in_count="draw_array.length" @back_to_clickpage="self_update_status"/>
        </div>
        <div class="tab-pane fade" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
          <br>
          <Setting @updated_count_and_type="self_update_count_and_type" :draw_count="sellected_times" :draw_type="sellected_type"/>
        </div>
      </div>
      
      <br>
    </div>
    
  </div>
</template>

<script>
import Clickbtn from './components/Clickbtn'
import Show from './components/Show'
import Setting from './components/Setting'

export default {
  name: 'App',
  components: {
    Clickbtn,
    Show,
    Setting
  },
  data(){
    return{
      draw_array: [],
      status:0,
      sellected_type:"0",
      sellected_times:"1",
      total_count:0,
      total_count_no:0,
      total_each_count: [
        {id:0, count:0},
        {id:1, count:0},
        {id:2, count:0},
        {id:3, count:0},
        {id:4, count:0},
        {id:5, count:0},
        {id:6, count:0},
        {id:7, count:0}
      ]
    }
  },
  methods:{
    self_update_draw({array,count,total_array,no_count}){
      this.draw_array=array;
      this.total_count=count;
      this.total_each_count=total_array;
      this.total_count_no = no_count;
    },
    self_update_status(val){
      this.status = val;
    },
    self_update_count_and_type({count, type}){
      this.sellected_type = type;
      this.sellected_times = count;
    }
  }
}
</script>

